import "../styles/fleetStatus.scss";
import carIcon from "../assets/car.png";
import { useState } from "react";
import { useContext } from "react";
import { LocationContext, DriverProps } from "../contexts/Location";
import api from "../services/api";
import { useEffect } from "react";

export function FleetStatus() {
  const [currentTab, setCurrentTab] = useState(0);
  const { drivers } = useContext(LocationContext);
  return (
    <div id="fleet-status-container">
      <div id="status-menu">
        <div
          onClick={() => setCurrentTab(0)}
          className={`status-menu-item ${currentTab === 0 ? "clicked" : ""}`}
        >
          <h3>Usuários</h3>
        </div>

        <div
          onClick={() => setCurrentTab(1)}
          className={`status-menu-item ${currentTab === 1 ? "clicked" : ""}`}
        >
          <h3>Mensagens</h3>
        </div>
      </div>
      <div id="content">
        {currentTab === 0 ? <VehiclesList content={drivers} /> : <MsgList />}
      </div>
    </div>
  );
}

function MsgList() {
  return (
    <ul id="msg-list">
      <li>
        <img
          src="https://lh3.googleusercontent.com/a-/AOh14GiwlWQKHCsOvn5sAP98V_tADQHNMiGZe9j_D5pUCw=s96-c"
          alt="profile"
        />
        <div id="msg-info1">
          <h3>Bruno Good</h3>
          <p>Boa tarde</p>
        </div>
        <div id="msg-info2">
          <p>14:30</p>
          <div id="msg-ball"></div>
        </div>
      </li>
    </ul>
  );
}

type Params = {
  content: DriverProps[];
};

function getElapsedTime(timestamp: string) {
  const totalSeconds = (Date.now() - parseInt(timestamp)) / 1000;

  const hours = Math.floor(totalSeconds / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = Math.floor((totalSeconds % 3600) % 60);

  if (hours !== 0) {
    return `${hours}h`;
  } else if (minutes !== 0) {
    return `${minutes}Min`;
  } else return `${seconds}S`;
}

function Last({ driverId }: { driverId: string }) {
  const [lasCord, setLastCord] = useState("");

  const getDriverLocationTime = async (id: string) => {
    const result = await api.get(`last_coordinate/driver_id=${id}`);
    if (result) {
      setLastCord(getElapsedTime(result.data[0].timestamp));
    } else {
      setLastCord("indefinido");
    }
  };

  useEffect(() => {
    getDriverLocationTime(driverId);
  }, [driverId]);

  return <p>{`Última atualização: ${lasCord}`}</p>;
}

function VehiclesList({ content }: Params) {
  const { setCordsOnFocus } = useContext(LocationContext);

  return (
    <ul id="vehicles-list">
      {content.map((i) => {
        return (
          <li onClick={() => setCordsOnFocus(i.driverId)} key={i.driverId}>
            <img src={carIcon} alt="carIcon" />
            <div className="text">
              <h4>{i.driverName}</h4>
              <Last driverId={i.driverId} />
            </div>
            <strong>90km/h</strong>
            <div className="status-ball"></div>
          </li>
        );
      })}
    </ul>
  );
}
